.App {
    background-image: url(./tausta3.png);
    min-height: 100vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    background-size: cover;
    align-items: center;
    display: flex;
    flex-direction: column;
}


.wrapper {
  margin-top: 6em;
}

#wrap {
  justify-content: center;
  overflow: hidden
  
}
#napit1 {
  float:left;
}
#napit2 {
  float:right;
}

#kuvitus {
  margin-top: 1em;
  margin-left: 1em;
  margin-right: 2em;
  width: 30em;
  height: auto;
  float:right;
}

  
.btn {
    background-color: darkslategrey;
    color: white;
    padding: 2em;
    margin: 1em;
    width: 12em;
    
}

.sivu {
  color: white;
  background-color:rgba(0, 0, 0, 0.7);
  margin: 3em;
  margin-top: 6em;
  padding: 2em;
}

@media only screen and (max-width: 1120px) {
  #kuvitus {
    width: 20em;
  }
}

@media only screen and (max-width: 768px) {
  #napit1 { 
   float:none;
 }
 #napit2 { 
  float:none;
  }
  #kuvitus {
      display: none;
    }

}

